body {
    background-color: #E5EDF3;
    background-size: cover;
    display: flex;
    height: 100vh;
    width: 100vw;
}
.main-container{
    display: flex;
    flex-direction: column;
    background-color: #E5EDF3;
    height: 100vh;
    width: 100vw;
}
.start-button-container {
    background-color: #E5EDF3;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 50vh;
    padding-right: 10px;
    padding-top: 5%;
    margin-top: 0%;
}
  
.start-button {
    /* padding: 12px 24px; */
    width: 15%;
    height: 15%;
    font-size: 30px;
    background-color: #6272AD;
    color: #fff;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    font-family: 'Iropke Batang', serif;
    font-weight: bold;
}
.top{
    display: flex;
    justify-content: flex-start;
    /* margin-left: 0;
    padding-left: 0; */
}
@import url('//cdn.jsdelivr.net/font-iropke-batang/1.2/font-iropke-batang.css');
.top-text{
    /* font-weight: 300; */
    font-size: 150%;
    width: 350px;
    margin-left: 5%;
    color: #778399;
    font-family: 'Iropke Batang', serif;
    margin-top: 2%;
    margin-bottom: 0px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.top-text2{
    margin-top: 0px;
    margin-bottom: 0px;
}
.title1{
    font-family: 'Iropke Batang', serif;
    font-size: xx-large;
    color: #6272AD;
}
.blank{
    width: 400px;
}
.image-bot2{
    background-image: url('./images/robot.png');
    width: 50px;
    height: 50px;
    padding: 12px 15px;
    margin-left: 10px;
    background-repeat: no-repeat;
    background-size: contain;
}
  