    height: 100vh;
    width: 100vw;
    background-color: #E5EDF3;
    border-radius: 10px; /* 모서리를 둥글게 조정 */
    border-color: black;
    border-width: 2px;
    padding: 0%;
}

.chat-window {
    height: 600px;
    overflow-y: scroll;
    /* border: 1px solid #ccc; */
    padding: 20px;
    border-color: rgb(220, 220, 220);
    background-color: #E5EDF3;
}

.message {
    padding-left: 20px;
    border-radius: 15px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-bottom: 20px;
    
}

.user-message {
    background-color: #6272AD;
    /* 사용자 메시지를 오른쪽에 붙이도록 조정 */
    color : white;
    width: auto;
    max-width: 80%;
    min-width: 50%;
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    margin-left: auto; /* 오른쪽으로 붙이기 위해 margin-left를 auto로 설정 */
    margin-top: 10px;
    font-family: 'Iropke Batang', serif;
}
.user2-message {
    /* background-color: #6272AD; */
    /* 사용자 메시지를 오른쪽에 붙이도록 조정 */
    /* color : white; */
    width: auto;
    max-width: 80%;
    /* min-width: 50%; */
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    margin-left: auto; /* 오른쪽으로 붙이기 위해 margin-left를 auto로 설정 */
    margin-top: 10px;
    font-family: 'Iropke Batang', serif;
}

.user-message p {
    word-wrap: break-word;
    min-width: 50%;
}

.bot-message {
    background-color: #EBF5FF;
    /* 챗봇 메시지를 왼쪽에 붙이도록 조정 */
